<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" class="demo-form-inline">
				<el-form-item label="数据类型" label-width="90px">
				    <el-select placeholder="请选择数据类型" v-model="searchData.dataType">
				    	<el-option label="全部" value="-1"></el-option>
				    	<el-option v-for="item in searchData.dataTypeList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="数据导入者" label-width="120px">
				    <el-select placeholder="请选择数据导入者" filterable  v-model="searchData.dataAuthor">
				    	<el-option label="全部" value="-1"></el-option>
				    	<el-option v-for="item in searchData.dataAuthorList" :label="item.name" :value="item.userId"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="导入时间" label-width="110px">
				    <el-date-picker v-model="searchData.dataRange" type="datetimerange"  placeholder="选择时间范围" align="right">
    				</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getData()">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="dataImport()">导入数据</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<template>
			<el-table :data="tableData" fit highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<el-table-column prop="dataTypeName" label="数据类型" min-width="160"></el-table-column>
				<el-table-column prop="operator" label="数据导入者" min-width="120"></el-table-column>
				<el-table-column prop="createTime" :formatter="formatCreateTime" label="导入时间" min-width="180"></el-table-column>
				<el-table-column prop="totalSize" label="请求导入数据量" min-width="160"></el-table-column>
				<el-table-column prop="status" label="状态" min-width="100" :formatter="formatStatus"></el-table-column>
				<el-table-column prop="processCompletedTime" label="完成时间" min-width="180" :formatter="formatcompleteTime"></el-table-column>
				<el-table-column prop="successSize" label="导入成功数" min-width="120" :formatter="formatOnline"></el-table-column>
				<el-table-column prop="failureSize" label="导入失败数" min-width="120" :formatter="formatOnline"></el-table-column>
				<el-table-column min-width="80" fixed="right" label="操作">
					<template scope="scope">
      					<a class="download2" v-if="scope.row.downloadPath != '' && scope.row.downloadPath != null" type="text" size="small" download :href="'/api' + scope.row.downloadPath">下载</a>
						<el-button v-else type="text" size="small">暂无操作</el-button>
      				</template>
				</el-table-column>
			</el-table>
		</template>

		<!--分页-->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1"　:page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper"　:total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
		<!-- 数据导入弹窗 -->
		<el-dialog :title="importForm.formTitle" v-model="importForm.formVisible" :close-on-click-modal="false">
			<el-form id="form" :model="importForm" label-width="100px">
				<el-form-item label="数据类型" label-width="110px">
				    <el-select placeholder="请选择数据类型" v-model="importForm.dataType" @change="changeType()">
				    	<el-option v-for="item in importForm.dataTypeList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				    <a v-if="importForm.dataType != ''" class="download" download :href="importForm.filePath">下载数据模板</a>
				    <span v-else class="download" @click="noTypeSelect()">下载数据模板</span>
				</el-form-item>
				<el-form-item label="选择文件" label-width="110px">
				    <el-upload ref="upload" :auto-upload="false" action="/api/data/import/" :headers="headersInfor" :file-list="importForm.fileList" :data="uploadData" :multiple="false" :on-success="uploadSuccess" :on-error="uploadError">
  						<el-button size="small" type="primary">选择文件</el-button>
					</el-upload>
				</el-form-item>
			</el-form>


			<div slot="footer" class="dialog-footer">
				<el-button @click.native="importForm.formVisible = false">取 消</el-button>
				<el-button type="primary" @click="saveImport()">确定导入</el-button>
			</div>
		</el-dialog>

	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
	created:function () {
		this.getDataType();
		this.getDataAuthor();
	    this.getData();
    },
    data() {
        return {
        	/*搜索条件数据*/
        	searchData : {
        		dataType: '',//数据类型
        		dataTypeList : [],//数据类型列表
        		dataAuthor : '',//数据导入者
        		dataAuthorList : [],//数据导入者列表
        		dataRange : ''//时间范围
        	},
			tableData: [],
			pagerData : {
				total : 0,
				pageIndex : 1,
				pageSize : 10
			},
			importForm: {
				formTitle : '导入数据',
				formVisible : false,
				dataType: '',
        		dataTypeList : [],
        		fileList: [],
				filePath : 'javascript:void(0)'
			},
			uploadData : {
				dataType : ''
			},
			headersInfor: {
				token: ''
			}
			
		}
    },
    methods: {
        handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        this.getData();
        },
        /*格式化状态*/
        formatStatus: function(row ,column){
        	let statusName = '';
	  		switch(row.status)
			{
			case 0:
			  statusName = '处理中';
			  break;
			case 1:
			  statusName = '成功';
			  break;
		    case 2:
			  statusName = '导入失败';
			  break;
			case 3:
			  statusName = '解析失败';
			  break;
			default:
				statusName = '';
			}
			return statusName;
	    },
	    /*格式化时间*/
	    formatCreateTime: function(row,column){
	    	let ceratTime = new Date(row.createTime);
	    	return ceratTime.toLocaleDateString().replace(/\//g, "-") + " " + ceratTime.toTimeString().substr(0, 8);
	    },
	    formatcompleteTime: function(row,column){
	    	let ceratTime = new Date(row.processCompletedTime);
	    	return ceratTime.toLocaleDateString().replace(/\//g, "-") + " " + ceratTime.toTimeString().substr(0, 8);
	    },
        /*获取数据类型*/
        getDataType: function(){
        	let _this = this;
        	$.ajax({
				type: 'POST',
				data:"1001",
				url: "/api/data/import/dropdownlist",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							if(data.code == 0){
								_this.searchData.dataTypeList = data.body;
								_this.importForm.dataTypeList = data.body;
							}
						});
					}
				}
			});
        },
        /*获取数据导入者*/
        getDataAuthor: function(){
        	let _this = this;
        	$.ajax({
				type: 'POST',
				url: "/api/data/import/user_list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							if(data.code == 0){
								_this.searchData.dataAuthorList = data.body;
							}
						});
					}
				}
			});
        },
        /*获取列表*/
        getData: function(){
        	let _this = this;
        	let postData = {
        		pageIndex : _this.pagerData.pageIndex,
        		pageSize : _this.pagerData.pageSize,
        		dataType : _this.searchData.dataType,
        		operator : _this.searchData.dataAuthor,
        		beginTime : util.formatTime(_this.searchData.dataRange[0]),
        		endTime : util.formatTime(_this.searchData.dataRange[1])
        	}
        	$.ajax({
				type: 'POST',
				data: JSON.stringify(postData),
				url: "/api/data/import/record_list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							if(data.code == 0){
								_this.tableData = data.body.list;
								_this.pagerData.total = data.body.totalSize;
							}
						});
					}
				}
			});
        },
        /*数据导入*/
        dataImport: function(){
        	this.importForm.formVisible = true;
        },
        changeType: function(){
        	let _this = this;
        	let type = _this.importForm.dataType;
        	if(type == "1001001"){
        		_this.importForm.filePath = "/api/optfile/excel/template/土地价格指数.xlsx";
        	}else if(type == "1001002"){
        		_this.importForm.filePath = "/api/optfile/excel/template/价格指数.xlsx";
        	}else{
        		_this.importForm.filePath = "/api/optfile/excel/template/二手房价格指数.xlsx";
        	}
        },
        saveImport: function(){
        	this.uploadData.dataType = this.importForm.dataType;
        	this.headersInfor.token = util.getToken();
        	this.$refs.upload.submit();
        },
        uploadSuccess: function(response, file, fileList){
        	let _this = this;
        	if(response.code == 0){
        		_this.$notify({title: '成功',message: response.msg, type: 'success',duration: 2000});
        	}else{
        		_this.$notify({title: '错误',message: response.msg, type: 'error',duration: 2000});
        	}
        	_this.importForm.formVisible = false;
        	_this.getData();
        },
        uploadError: function(){
        	_this.$notify({title: '错误',message: "数据导入失败！", type: 'error',duration: 2000});
        },
        //未选择类型时下载提示
        noTypeSelect: function(){
        	this.$notify({title: '错误',message: "请选择数据类型！", type: 'error',duration: 2000});
        }
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	.toolbar {
		background: #fff;
	}
	.download{text-decoration: underline; color:#20a0ff; font-size: 14px; cursor: pointer; margin-left: 10px;}
	.download2{text-decoration: none; color:#20a0ff; font-size: 12px;}
</style>




