<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item label="角色：" label-width="90px">
				    <el-select placeholder="请选择角色" v-model="searchData.roleName">
				    	<el-option label="全部" value="-1"></el-option>
				    	<el-option v-for="item in searchData.roleList" :label="item.roleName" :value="item.id"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="账号/姓名：" label-width="90px">
				    <el-input v-model="searchData.searchKey" placeholder="请输入账号或姓名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getData">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="hadleAdd">导入账号</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<template>
			<el-table :data="tableData" fit highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<!-- <el-table-column  type="selection" min-width="55"> </el-table-column> -->
				<el-table-column prop="userId" label="账号" min-width="160"></el-table-column>
				<el-table-column prop="name" label="姓名" min-width="130"></el-table-column>
				<el-table-column prop="email" label="邮箱" min-width="200"></el-table-column>
				<el-table-column prop="phone" label="手机号" min-width="140"></el-table-column>
				<el-table-column prop="roleName" label="角色" min-width="180"></el-table-column>
				<el-table-column prop="valid" label="状态" :formatter="formatStatus"></el-table-column>
				<el-table-column prop="isOnline" label="在线信息" min-width="100" :formatter="formatOnline"></el-table-column>
				<el-table-column inline-template :context="_self" min-width="160" fixed="right" label="操作">
					<span>
						<el-button type="text" size="small" @click="editUser(row)">修改</el-button>
						<el-button type="text" size="small" @click="bindMacFoo(row)">绑定 MAC地址</el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!--分页-->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1"　:page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper"　:total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
		
		<!-- 新增&&编辑弹框 -->
		<el-dialog :title="addDialogData.editFormTtile" v-model="addDialogData.editFormVisible" size="tiny">
		    <el-form label-width="100px" :model="addDialogData" :rules="userRules" ref="userForm">
				<el-form-item label="姓名：" prop="name">
					<el-autocomplete :disabled="addDialogData.userNameDisable"　v-model="addDialogData.name"　:fetch-suggestions="querySearchAsync"　placeholder="请输入姓名"　@select="seltctUser"></el-autocomplete>
				</el-form-item>
				<el-form-item label="所在部门：" style="display:none;">
					<el-input auto-complete="off" :disabled="true"  v-model="addDialogData.department"></el-input>
				</el-form-item>
				<el-form-item label="账号：" prop="count">
					<el-input auto-complete="off" :disabled="true"  v-model="addDialogData.count"></el-input>
				</el-form-item>
				<el-form-item label="邮箱：" prop="email">
					<el-input auto-complete="off" :disabled="true"  v-model="addDialogData.email"></el-input>
				</el-form-item>
				<el-form-item label="手机号：" prop="phone">
					<el-input auto-complete="off" :disabled="true"  v-model="addDialogData.phone"></el-input>
				</el-form-item>
				<el-form-item label="状态：" v-if="addDialogData.isShowStatus">
					<el-radio-group v-model="addDialogData.status">
						<el-radio class="radio" :label="1">正常</el-radio>
						<el-radio class="radio" :label="0">已注销</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="角色：" prop="rolesValue">
					<el-select v-model="addDialogData.rolesValue" multiple placeholder="请选择">
				    	<el-option v-for="item in addDialogData.roles" :label="item.roleName" :value="item.id">
				    	</el-option>
				    </el-select>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addDialogData.editFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="saveUserInfo">确 定</el-button>
		  </span>
		</el-dialog>
		
		<!-- 绑定MAC地址弹框 -->
		<el-dialog :title="bindMac.title" v-model="bindMac.visible" size="tiny">
		    <el-form label-width="100px" :model="bindMac" :rules="macRules" ref="bindMac">
				<el-form-item v-for="(v, i) in bindMac.macList" label="">
					<el-input v-model="v.value" class="mac-input" :disabled=true></el-input><el-button class="mac-botton" @click.prevent="removeMac(v)">删除</el-button>
  				</el-form-item>
  				<el-form-item label="MAC地址：" prop="addMacData">
					<el-input class="mac-input" v-model="bindMac.addMacData"></el-input>
					<el-button class="mac-botton" @click="addMac('bindMac')">添加</el-button>
  				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="bindMac.visible = false">取 消</el-button>
		    <el-button type="primary" @click="saveMac">确 定</el-button>
		  </span>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
	created:function () {
	    this.getData();
	    this.getRoleList();
    },
    data() {
    	var checkMac = (rule, value, callback) => {
	        if (!value) {
	          return callback(new Error('MAC地址不能为空！'));
	        }else{
	        	callback();
	        }
	        /*var _this = this;
        	var macArr = [];
        	var reg_name=/[A-F\d]{2}:[A-F\d]{2}:[A-F\d]{2}:[A-F\d]{2}:[A-F\d]{2}:[A-F\d]{2}/; 
    		if(_this.bindMac.macList.length > 0){
  				$.each(_this.bindMac.macList, (i, v) => {
	  				macArr.push(v.value);
	  			});
  			}*/
  			/*if(macArr.includes(value)){
  				callback(new Error('MAC地址重复！'));
  			}else if(!reg_name.test(value)){
  				callback(new Error('MAC地址格式不正确！'));
  			}else{
  				callback();
  			}*/
  			/*callback();*/
	    };
        return {
			addDialogData: {
				editFormTtile : '导入用户信息',
				editFormVisible : false,
				isShowStatus : false,
				userNameDisable : false,
				name : '',
				department : '',
				count : '',
				email : '',
				phone : '',
				companyid : '',
				status : '',
				roles: [],
				rolesValue: [],
				userList : [],
				timeout: null
			},
			tableData: [],
			listLoading:false,
			bindMac: {
				id : '',
				title: '绑定MAC地址',
				visible: false,
				addMacData: '',
				macList: [],
				userId : ''
			},
			pagerData : {
				total : 0,
				pageIndex : 1,
				pageSize : 10
			},
			searchData: {
				searchKey : '',
				roleName: '',
				roleList: []
			},
			macRules: {
	          addMacData: [
	            {required: true, validator: checkMac, trigger: 'change' }
	          ]
	        },
	        userRules: {
	        	name:[{required: true, message: '请输入姓名', trigger: 'change'}],
	        	count:[{required: true, message: '请输入账号', trigger: 'change'}],
	        	email:[{required: true, message: '请输入邮箱', trigger: 'change'}],
	        	phone:[{required: true, message: '请输入手机号', trigger: 'change'}],
	        	rolesValue:[{type: 'array', required: true, message: '请选择角色', trigger: 'change'}]
	        }
		}
    },
    methods: {
        handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        this.getData();
        },
     	 /*获取列表数据*/
	    getData: function(){
			var _self  = this;
			var postData = {
				pageIndex : _self.pagerData.pageIndex,
				pageSize : _self.pagerData.pageSize,
				searchKey : _self.searchData.searchKey,
				roleId : _self.searchData.roleName
			}
			$.ajax({
				type: 'POST',
				data:JSON.stringify(postData),
				url: "/api/user/list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _self, function(){
							_self.tableData = data.body.list;
							_self.pagerData.total = data.body.totalSize;
						});
					}
				}
			});
	    },
	    /*获取角色列表*/
	    getRoleList: function(){
	    	let _this = this;
	    	$.ajax({
				type: 'POST',
				url: "/api/user/role_list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							_this.addDialogData.roles = data.body;
							_this.searchData.roleList = data.body;
						});
					}
				}
			});
	    },
	  	/*格式化用户状态*/
	    formatStatus: function(row,column){
	  		return row.valid == 1 ? '正常' : '已注销';
	    },
	 	/*格式化在线信息*/
	    formatOnline: function(row,column){
	  		return row.isOnline == 1 ? '在线' : '离线';
	    },
	  	/*添加用户方法*/
	    hadleAdd:function(){
		  	this.addDialogData.editFormTtile = '导入用户信息';
		  	this.addDialogData.editFormVisible = true;
		  	this.addDialogData.isShowStatus = false;
		  	this.addDialogData.userNameDisable = false;
		  	this.addDialogData.name = "";
		  	this.addDialogData.department = "";
		  	this.addDialogData.count = "";
		  	this.addDialogData.email = "";
		  	this.addDialogData.phone = "";
		  	this.addDialogData.status = "";
		  	this.addDialogData.id = -1;
		  	this.addDialogData.rolesValue = [];
	    },
	  	/*修改用户方法*/
	    editUser: function(row){
		  	this.addDialogData.editFormTtile = '修改用户信息';
		  	this.addDialogData.editFormVisible = true;
		  	this.addDialogData.isShowStatus = true;
		  	this.addDialogData.userNameDisable = true;
		  	this.addDialogData.name = row.name;
		  	this.addDialogData.department = row.departmentName;
		  	this.addDialogData.count = row.userId;
		  	this.addDialogData.email = row.email;
		  	this.addDialogData.phone = row.phone;
		  	this.addDialogData.status = row.valid;
		  	this.addDialogData.id = row.id;
		  	this.addDialogData.companyid = row.companyId;
		  	if(row.roleId != null){
		  		this.addDialogData.rolesValue = row.roleId.split(",");
		  	}
		  	
	    },
	  	/*绑定MAC地址方法*/
	    bindMacFoo: function(row){
	  		this.bindMac.visible = true;
	  		this.bindMac.id = row.id;
	  		this.bindMac.addMacData = '';
	  		this.bindMac.macList = [];
	  		this.bindMac.userId = row.userId;
	  		if(row.macAddress && row.macAddress != null && row.macAddress != ""){
	  			var macArr = row.macAddress.split(",");
	  			if(macArr.length > 0){
	  				$.each(macArr, (i, v) => {
		  				this.bindMac.macList.push({value: v});
		  			});
	  			}
	  		}
	    },
	    removeMac: function(item) {
	    	var index = this.bindMac.macList.indexOf(item)
	        if (index !== -1) {
	        	this.bindMac.macList.splice(index, 1)
	        }
    	},
    	addMac: function(formName){
    		this.$refs.bindMac.validate((valid) => {
    			if(valid){
		  			var macData = {value: this.bindMac.addMacData};
	    			this.bindMac.macList.push(macData);
	    			this.bindMac.addMacData = '';
    			}
    		})
    	},
    	saveMac: function(){
    		var _this = this;
    		var macArr = [];
    		if(_this.bindMac.macList.length > 0){
  				$.each(_this.bindMac.macList, (i, v) => {
	  				macArr.push(v.value);
	  			});
  			}
  			if(_this.bindMac.addMacData != '' && _this.bindMac.addMacData != null){
  				macArr.push(_this.bindMac.addMacData);
  			}
    		var macStr = macArr.join(",");
    		var saveData = {
    			id: this.bindMac.id,
    			macAddress : macStr,
    			userId : this.bindMac.userId
    		}
    		if(macStr.length <= 0){
    			_this.$notify({title: '错误',message: "MAC地址不能为空！", type: 'error',duration: 2000});
    			return false;
    		}
    		$.ajax({
				type: 'POST',
				data: saveData,
				url: "/api/user/binding_mac",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					util.checkLogin(data.code, _this, function(){
						if(data && data.code == 0){
							_this.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
							_this.getData();
						}else{
							_this.$notify({title: '错误',message: data.msg, type: 'error',duration: 2000});
						}
						_this.bindMac.visible = false;
					});
				}
			});
    	},
    	/*查询用户中心数据方法*/
    	querySearchAsync(queryString, cb) {
    		var _this = this;
        	clearTimeout(this.timeout);
        	this.timeout = setTimeout(() => {
        		$.ajax({
					type: 'POST',
					data: queryString ? queryString : "fxt",
					url: "/api/user/uc_list",
					contentType: "application/json",
					headers: {'token': util.getToken()},
					dataType: 'json',
					//async: false,
					success: function(data){
						if(data && data != null){
							util.checkLogin(data.code, _this, function(){
								_this.addDialogData.userList = data.body;
								var userList = [];
					        	$.each(_this.addDialogData.userList, (i, v)=>{
					        		userList.push({
					        			"value" : v.truename
					        		});
					        	});
					        	var results = userList;
					            cb(results);
							});
						}
					}
				});
	        }, 1000);
	    },
	    seltctUser(item) {
	        var _this = this;
	        $.each(_this.addDialogData.userList, (i, v) =>{
	        	if(v.truename === item.value){
	        		_this.addDialogData.count = v.username;
	        		_this.addDialogData.email = v.emailstr;
	        		_this.addDialogData.phone = v.mobile;
	        		_this.addDialogData.companyid = v.companyid;
	        		return false;
	        	}
	        });
	    },
	    saveUserInfo: function(){
	    	var _this = this;

	    	this.$refs.userForm.validate((valid) => {
	            if (valid) {
	            	var saveData = null;
				    var url = "";
				    if(_this.addDialogData.id == -1){
				    	url = "/api/user/insert";
				    	saveData = {
					    	userId : _this.addDialogData.count,
					    	name : _this.addDialogData.name,
					    	email : _this.addDialogData.email,
					    	phone : _this.addDialogData.phone,
					    	companyId : _this.addDialogData.companyid,
					    	roleId : _this.addDialogData.rolesValue.join(',')
					    }
				    }else{
				    	url = "/api/user/modify";
				    	saveData = {
					    	userId : _this.addDialogData.count,
					    	name : _this.addDialogData.name,
					    	email : _this.addDialogData.email,
					    	phone : _this.addDialogData.phone,
					    	companyId : _this.addDialogData.companyid,
					    	roleId : _this.addDialogData.rolesValue.join(','),
					    	id : _this.addDialogData.id,
					    	valid : _this.addDialogData.status
					    }
				    }
			    	$.ajax({
						type: 'POST',
						data: JSON.stringify(saveData),
						url: url,
						contentType: "application/json",
						headers: {'token': util.getToken()},
						dataType: 'json',
						success: function(data){
							util.checkLogin(data.code, _this, function(){
								if(data && data.code == 0){
									_this.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
									_this.getData();
								}else{
									_this.$notify({title: '错误',message: data.msg, type: 'error',duration: 2000});
								}
							});
							
							_this.addDialogData.editFormVisible = false;
						}
					});
	            }
		     })
	    }
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	.toolbar {
		background: #fff;
	}
	.mac-input{float:left; width:180px; margin-right:15px;}
	
</style>




