<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" class="demo-form-inline">
				<el-form-item label="业务来源" label-width="90px">
				    <el-select placeholder="请选择业务来源" v-model="searchData.source">
				    	<el-option label="全部" value="-1"></el-option>
				    	<el-option v-for="item in searchData.sourceList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="业务类型" label-width="90px">
				    <el-select placeholder="请选择业务类型" v-model="searchData.type">
				    	<el-option label="全部" value="-1"></el-option>
				      <el-option v-for="item in searchData.typeList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="公司" label-width="60px">
				    <el-select placeholder="请选择公司" filterable v-model="searchData.company">
				      <el-option v-for="item in searchData.companyList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="导出时间" label-width="90px">
				    <el-date-picker v-model="searchData.dataRange" type="datetimerange" placeholder="选择时间范围" align="left" format="yyyy-MM-dd HH:mm:ss">
    				</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getDataList()">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="exportData()">导出数据</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<template>
			<el-table :data="tableData" fit highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<el-table-column prop="businessSourceName" label="业务来源" min-width="100"></el-table-column>
				<el-table-column prop="businessTypeName" label="业务类型" min-width="160"></el-table-column>
				<el-table-column prop="companyName" label="公司" min-width="240"></el-table-column>
				<el-table-column prop="recordTime" label="数据记录时间" min-width="360"></el-table-column>
				<el-table-column prop="createTime" :formatter="formatCreateTime" label="导出时间" min-width="180"></el-table-column>
				<el-table-column prop="size" label="数据量" min-width="80"></el-table-column>
				<el-table-column prop="status" label="状态" :formatter="formatStatus"></el-table-column>
				<el-table-column prop="operator" label="数据导出者" min-width="120"></el-table-column>
				<el-table-column min-width="80" fixed="right" label="操作">
					<template scope="scope">
      					<a class="download" v-if="scope.row.downloadPath != '' && scope.row.downloadPath != null" type="text" size="small" download :href="'/api' + scope.row.downloadPath">下载</a>
						<el-button v-else type="text" size="small">暂无操作</el-button>
      				</template>
				</el-table-column>
			</el-table>
		</template>

		<!--分页-->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1"　:page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper"　:total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
		
		<!-- 数据导出弹窗 -->
		<el-dialog :title="exprtForm.formTitle" v-model="exprtForm.formVisible" :close-on-click-modal="false">
			<el-form :model="exprtForm" label-width="100px">
				<el-form-item label="业务来源" label-width="110px">
				    <el-select placeholder="请选择业务来源" v-model="exprtForm.source">
				    	<el-option label="全部" value="-1"></el-option>
				    	<el-option v-for="item in exprtForm.sourceList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="业务类型" label-width="110px">
				    <el-select placeholder="请选择业务类型" v-model="exprtForm.type">
				    	<el-option label="全部" value="-1"></el-option>
				      <el-option v-for="item in exprtForm.typeList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="公司" label-width="110px">
				    <el-select placeholder="请选择公司" filterable v-model="exprtForm.company">
				      <el-option v-for="item in exprtForm.companyList" :label="item.label" :value="item.value"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="数据记录时间" label-width="110px">
				    <el-date-picker v-model="exprtForm.dataRange" type="datetimerange" placeholder="选择时间范围" align="left" format="yyyy-MM-dd HH:mm:ss">
    				</el-date-picker>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="exprtForm.formVisible = false">取 消</el-button>
				<el-button type="primary" @click="exportDataOk()">确定导出</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
	created:function () {
		let _this = this;
		_this.getBusinessSource(500, function(data){
			_this.searchData.sourceList = data.body;
			_this.exprtForm.sourceList = data.body;
		});
		_this.getBusinessSource(300, function(data){
			_this.searchData.typeList = data.body;
			_this.exprtForm.typeList = data.body;
		});
		_this.getCompany();
	    this.getDataList();
    },
    data() {
        return {
        	searchData : {
        		source : '',
        		type : '',
        		company : '',
        		dataRange : '',
        		sourceList : [],
        		typeList : [],
        		companyList : []
        	},
			tableData: [],
			exprtForm: {
				formTitle : '导出数据',
				formVisible : false,
				source : '',
        		type : '',
        		company : '',
        		dataRange : '',
        		sourceList : [],
        		typeList : [],
        		companyList : []
			},
			pagerData : {
				total : 0,
				pageIndex : 1,
				pageSize : 10
			}
		}
    },
    methods: {
        handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        this.getDataList();
        },
        /*格式化时间*/
	    formatCreateTime: function(row,column){
	    	let ceratTime = new Date(row.createTime);
	    	return ceratTime.toLocaleDateString().replace(/\//g, "-") + " " + ceratTime.toTimeString().substr(0, 8);
	    },
	    /*格式化状态*/
	    formatStatus: function(row,column){
	    	return row.status == 0 ? "处理中" : (row.status == 1 ? "成功" : "失败");
	    },
        /*获取业务来源和业务类型方法*/
        getBusinessSource: function(code, callBack){
        	let _this = this;
        	$.ajax({
				type: 'GET',
				data:{code:code},
				//async:false,
				url: "/api/data/export/dropdownlist",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							callBack(data);
						});
					}
				}
			});
        },
        /*获取公司列表方法*/
        getCompany: function(){
        	let _this = this;
        	$.ajax({
				type: 'GET',
				//async:false,
				url: "/api/data/export/company_list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							$.each(data.body, function(index, el) {
								_this.searchData.companyList.push({
									label : el.fxtCompanyName,
									value : el.fxtCompanyId
								});
								_this.exprtForm.companyList.push({
									label : el.fxtCompanyName,
									value : el.fxtCompanyId
								});
							});
						});
						
					}
				}
			});
        },
        /*获取数据列表方法*/
        getDataList: function(){
        	let _this = this;
        	let postData = {
        		businessSource : _this.searchData.source,
        		businessType : _this.searchData.type,
        		companyId : _this.searchData.company,
        		beginTime : util.formatTime(_this.searchData.dataRange[0]),
        		endTime : util.formatTime(_this.searchData.dataRange[1]),
        		pageIndex : _this.pagerData.pageIndex,
        		pageSize : _this.pagerData.pageSize
        	}
        	$.ajax({
				type: 'POST',
				data:JSON.stringify(postData),
				url: "/api/data/export/record_list",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							_this.tableData = data.body.list;
							$.each(_this.tableData, function(index, el) {
								let bt = new Date(el.beginTime);
								let et = new Date(el.endTime);
								el.recordTime = bt.toLocaleDateString().replace(/\//g, "-") + " " + bt.toTimeString().substr(0, 8) + "至" + et.toLocaleDateString().replace(/\//g, "-") + " " + et.toTimeString().substr(0, 8);
							});
							_this.pagerData.total = data.body.totalSize;
						});
					}
				}
			});
        },
        /*导出数据方法*/
        exportData: function(){
        	this.exprtForm.formVisible = true;
        },
        exportDataOk: function(){
        	let _this = this;
        	let postData = {
        		businessSource : _this.exprtForm.source,
        		businessType : _this.exprtForm.type,
        		companyId : _this.exprtForm.company,
        		beginTime : util.formatTime(_this.exprtForm.dataRange[0]),
        		endTime : util.formatTime(_this.exprtForm.dataRange[1])
        	}
        	let et = new Date(postData.endTime);
        	let bt = new Date(postData.beginTime);
        	let days = parseInt((et - bt) / (1000 * 60 * 60 * 24));
        	if(days > 31){
        		_this.$notify({title: '错误',message: "选择时间超过31天，无法导出！", type: 'error',duration: 2000});
        		return false;
        	}
        	$.ajax({
				type: 'POST',
				data:JSON.stringify(postData),
				url: "/api/data/export/",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					_this.exprtForm.source = '';
					_this.exprtForm.type = '';
					_this.exprtForm.company = '';
					_this.exprtForm.dataRange[0] = '';
					_this.exprtForm.dataRange[1] = '';
					_this.exprtForm.formVisible = false;
					if(data && data != null){
						util.checkLogin(data.code, _this, function(){
							if(data && data.code == 0){
								_this.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
								_this.getDataList();
							}else{
								_this.$notify({title: '错误',message: data.msg, type: 'error',duration: 2000});
							}
						});
					}
				}
			});
        }
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	.toolbar {
		background: #fff;
	}
	.download{text-decoration: none; color:#20a0ff; font-size: 12px;}
	
</style>




