<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" class="demo-form-inline">
				<el-form-item label="业务类型" label-width="90px">
				    <el-select placeholder="请选择业务类型" v-model="searchData.type">
				      <el-option label="VQ API自动估价" value="all"></el-option>
				      <el-option label="VQ API自动估价" value="c1"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="公司" label-width="60px">
				    <el-select placeholder="请选择公司"  v-model="searchData.company">
				      <el-option label="武汉农商行" value="all"></el-option>
				      <el-option label="武汉农商行" value="c1"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="数据记录时间" label-width="110px">
				    <el-date-picker v-model="searchData.dataRange" type="datetimerange" :picker-options="pickerOptions" placeholder="选择时间范围" align="right">
    				</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary">导出数据</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<template>
			<el-table :data="tableData" fit highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<el-table-column prop="businessType" label="业务类型" width="160"></el-table-column>
				<el-table-column prop="company" label="公司" width="140"></el-table-column>
				<el-table-column prop="dataRecordTime" label="数据记录时间" width="360"></el-table-column>
				<el-table-column prop="exportTime" label="导出时间" width="180"></el-table-column>
				<el-table-column prop="dataCount" label="数据量" width="80"></el-table-column>
				<el-table-column prop="status" label="状态" :formatter="formatStatus"></el-table-column>
				<el-table-column prop="author" label="数据导出者" width="120" :formatter="formatOnline"></el-table-column>
				<el-table-column inline-template :context="_self" width="80" fixed="right" label="操作">
					<span>
						<el-button type="text" size="small">下载</el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!--分页-->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1"　:page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper"　:total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
	created:function () {
	    //this.getData();
    },
    data() {
        return {
        	searchData : {
        		type : '',
        		company : '',
        		dataRange : ''
        	},
			tableData: [{
				businessType : 'VQ API自动估价',
				company : '武汉农商行',
				dataRecordTime : '2016-12-19 00:00:00 至 2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '处理中',
				author : 'Bruce'
			},{
				businessType : 'VQ API自动估价',
				company : '武汉农商行',
				dataRecordTime : '2016-12-19 00:00:00 至 2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '处理中',
				author : 'Bruce'
			},{
				businessType : 'VQ API自动估价',
				company : '武汉农商行',
				dataRecordTime : '2016-12-19 00:00:00 至 2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '处理中',
				author : 'Bruce'
			}],
			pagerData : {
				total : 50,
				pageIndex : 1,
				pageSize : 10
			},
			pickerOptions: {
		        shortcuts: [{
		            text: '最近一周',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
		              picker.$emit('pick', [start, end]);
		            }
		        }, {
		            text: '最近一个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
		              picker.$emit('pick', [start, end]);
		            }
		        }, {
		            text: '最近三个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
		              picker.$emit('pick', [start, end]);
		            }
		        }]
		    },
		}
    },
    methods: {
        handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        //this.getData();
        }
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	.toolbar {
		background: #fff;
	}
	
</style>




