<template>
  <el-form :model="loginform" :rules="rules2" ref="loginform" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
    <h3 class="title">VQ运营中心登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="loginform.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="loginform.checkPass" auto-complete="off" @keyup.enter.native="handleSubmit2" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="remmberPass" checked style="margin:0px 0px 35px 0px;">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>
import util from '../common/util'
  export default {
    created:function () {
      this.remmberPassword();
    },
    data() {
      return {
        loginform: {
          account: '',
          checkPass: ''
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        },
        remmberPass: true
      };
    },
    methods: {
      handleSubmit2(ev) {
        var _this=this;
        this.$refs.loginform.validate((valid) => {
          if (valid) {
            //记住密码
            if(_this.remmberPass){
                localStorage.setItem("account", _this.loginform.account);
                localStorage.setItem("checkPass", _this.loginform.checkPass);
            }else{
                localStorage.removeItem("account");
                localStorage.removeItem("checkPass");
            }
            $.ajax({
                type: 'POST',
                data:{
                  loginName : _this.loginform.account,
                  password : _this.loginform.checkPass
                },
                url: "/api/user/login",
                dataType: 'json',
                success: function(data){
                  if(data && data.code == 0){
                    localStorage.setItem("loginName", data.body.user.loginName);
                    localStorage.setItem("realName", data.body.user.realName);
                    localStorage.setItem("token", data.body.token);
                    //菜单权限
                    let permission = data.body.user.permission;
                    let menuList = [];
                    if(permission && permission.length > 0){
                      $.each(permission, (i, v) =>{
                        if(v.perms && v.perms.length > 0){
                          $.each(v.perms, (j, k)=>{
                            menuList.push(k.resourceName);
                          });
                        }
                      });
                    }
                    menuList = [...new Set(menuList)]
                    let permissionList = JSON.stringify(menuList);
                    localStorage.setItem("permission", permissionList);
                    console.log(permissionList);
                    let newRoutes = [];
                    //判断成功后跳转到哪个页面
                    let permissionData = $.parseJSON(localStorage.getItem("permission"));
                    if(permissionData != null && permissionData != undefined && permissionData.length > 0){
                      $.each(_this.$router.options.routes, function(index, el) {
                        $.each(el.children,function(i, v) {
                          if(permissionData.includes(v.name)){
                            newRoutes.push(v.path);
                          }
                        });
                      });
                    }
                    _this.$router.replace(newRoutes[0]);
                    window.location.reload();
                  }else{
                    _this.$notify({title: '错误',message: '用户名或密码错误！', type: 'error',duration: 2000});
                  }
                },
                error:function(){
                  _this.$notify({title: '错误',message: '用户名或密码错误！', type: 'error',duration: 2000});
                }
            });n
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //记住密码
      remmberPassword: function(){
          this.loginform.account = localStorage.getItem("account");
          this.loginform.checkPass = localStorage.getItem("checkPass");
      }
    }
  }
</script>

<style scoped>
  .card-box {
    padding: 20px;
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin-bottom: 20px;
    background-color: #F9FAFC;
    margin: 120px auto;
    width: 400px;
    border: 2px solid #8492A6;
  }
  
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  
  .loginform {
    width: 350px;
    padding: 35px 35px 15px 35px;
  }
</style>q


