<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item label="角色名称：" label-width="90px">
				    <el-input v-model="keywords" placeholder="请输入角色名称"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getRoleList">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="addRole">新增角色</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<template>
			<el-table :data="roleList" highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<el-table-column prop="roleName" label="角色名称">
				</el-table-column>
				<el-table-column prop="updateBy" label="最近修改人">
				</el-table-column>
				<el-table-column prop="updateDate" label="最近修改时间">
				</el-table-column>
				<el-table-column inline-template :context="_self" label="操作" min-width="100">
					<span>
					<el-button type="text" size="small" @click="editRole(row)">修改</el-button>
					<el-button type="text" size="small" style="margin-left: 0px" @click="deleteRole(row)">删除</el-button>
				</span>
				</el-table-column>
			</el-table>
		</template>

		<!--分页-->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1"　:page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper"　:total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
		
		
		<!--编辑界面-->
		<el-dialog :title="editFormData.formTitle" v-model="editFormData.formVisible" :close-on-click-modal="false">
			<el-form :model="editFormData" label-width="100px" :rules="roleRules" ref="editFormData">
				<el-form-item label="角色名称" prop="roleName">
					<el-input v-model="editFormData.roleName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="功能权限" prop="authority">
					<div style="width:100%;">
						<template v-for = "(v,i) in editFormData.authority">
							<div class="clearfix" style="border-bottom:1px solid #bfcbd9; width:100%">
								<div style="width:20%; float:left; font-weight:bold">
									<el-checkbox v-model="editFormData.checkList[i].isCheck" @change="checkAll(i)">{{v.menuName}}</el-checkbox>
								</div>
								<div style="width:80%; float:right;">
									<el-checkbox-group v-model="editFormData.checkList[i].list">
										<el-checkbox v-for="(item,index) in v.child" :label="item.menuName" name="" @change="checkAllSub(i)">{{item.menuName}}</el-checkbox>
									</el-checkbox-group>
								</div>
							</div>
						</template>
					</div>

				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormData.formVisible = false">取 消</el-button>
				<el-button type="primary" @click="saveRole">确定</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
  	created:function () {
	    this.getRoleList();
    },
    data() {
      return {
			keywords: '',
			roleList: [],	//角色列表数据
			editFormData: {	//编辑界面数据
				formTitle : '添加角色',
				formVisible : false,
				roleName : '',
				authority : [],
				checkList : [],
				isAdd : false,
				roleId : ''
			},
			listLoading:false,
			roleRules: {
	          roleName: [{required: true,message:'角色名称不能为空！', trigger: 'blur' }],
	          authority : [{type: 'array', required: true,message:'角色名称不能为空！', trigger: 'blur' }]
	        },
	        pagerData : {
				total : 0,
				pageIndex : 1,
				pageSize : 10
			}
		}
    },
	methods : {
		/*获取角色列表*/
		getRoleList: function(){
			let thiz = this;
			let postData = {
				roleName : thiz.keywords,
				pageIndex : thiz.pagerData.pageIndex,
				pageSize : thiz.pagerData.pageSize
			}
			$.ajax({
				type: 'POST',
				url: "/api/role/findRolesWithPerms",
				dataType: 'json',
				headers: {'token': util.getToken()},
				data: postData,
				success: function(data){
					util.checkLogin(data.code, thiz, function(){
						if(data && data != null && data.code == 0){
							thiz.roleList = data.body.list;
							thiz.pagerData.total = data.body.totalSize;
						}
						thiz.listLoading = false;
					});
				},
				error: function(){
				}
			});
		},
		/*分页*/
		handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        this.getRoleList();
        },
        //全选
        checkAll: function(i){
        	let _this = this;
        	if(this.editFormData.checkList[i].isCheck){
        		$.each(this.editFormData.authority[i].child, function(index, el) {
        			_this.editFormData.checkList[i].list.push(el.menuName);
        		});
        		_this.editFormData.checkList[i].list = [...new Set(_this.editFormData.checkList[i].list)]
        	}else{
        		this.editFormData.checkList[i].list = [];
        	}
        },
        checkAllSub: function(i){
        	let cl = this.editFormData.checkList[i].list.length;
        	let al = this.editFormData.authority[i].child.length;
        	if(cl > 0){
        		this.editFormData.checkList[i].isCheck = true;
        	}else{
        		this.editFormData.checkList[i].isCheck = false;
        	}
        },
		/*获取目录列表*/
		getMenu: function(){
			let thiz = this;
			$.ajax({
				type: 'POST',
				url: "/api/menu/getAllMenu",
				contentType: "application/json",
				headers: {'token': util.getToken()},
				async: false,
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						util.checkLogin(data.code, thiz, function(){
							thiz.editFormData.authority = data;
							thiz.editFormData.checkList = [];
							$.each(thiz.editFormData.authority,function(index, el) {
								thiz.editFormData.checkList.push({
									isCheck:false,
									list:[]
								});
							});
						});
					}
				},
				error: function(){
				}
			});
		},
		/*新增角色方法*/
		addRole:function(){
			let thiz = this;
			thiz.editFormData.isAdd = false;
			thiz.editFormData.formVisible = true;
			thiz.editFormData.formTitle = '新增角色';
			thiz.editFormData.roleName = "";
			thiz.getMenu();
		},
		/*修改角色方法*/
		editRole: function(row){
			let thiz = this;
			thiz.editFormData.isAdd = true;
			thiz.editFormData.formVisible = true;
			thiz.editFormData.formTitle = '修改角色';
			thiz.getMenu();
			thiz.editFormData.roleName = row.roleName;
			thiz.editFormData.roleId = row.id;
			$.each(row.perms, function(index, el) {
				$.each(thiz.editFormData.authority, function(i, v) {
					if(el.resourceName === v.menuName){
						thiz.editFormData.checkList[i].isCheck = true;
					}
					$.each(v.child, function(j, k) {
						if(el.resourceName === k.menuName){
							thiz.editFormData.checkList[i].list.push(el.resourceName);
						}
					});
				});
			});
		},
		/*保存角色方法 */
		saveRole : function(){
			this.$refs.editFormData.validate((valid) => {
    			if(valid){
		  			let [thiz,names,resources] = [this,[],[]];
					$.each(thiz.editFormData.checkList,function(index, el) {
						if(el.isCheck === true){
							resources.push({
								resourceId : thiz.editFormData.authority[index].id,
								resourceName : thiz.editFormData.authority[index].menuName,
								resourceType : "MENU"
							});
							names.push(thiz.editFormData.authority[index].menuName);
						}
						$.each(el.list,function(i, v) {
							names.push(v);
							resources.push({
								resourceId : '',
								resourceName : v,
								resourceType : "MENU"
							});
						});
					});
					$.each(resources, function(index, el) {
						$.each(thiz.editFormData.authority,function(i, v){
							$.each(v.child, function(j, k) {
								if(el.resourceName === k.menuName){
									el.resourceId = k.id
								}
							});
						});
					});
					if(names.length <= 0){
						thiz.$notify({title: '错误',message: "请至少选择一个功能权限！", type: 'error',duration: 2000});
						return false;
					}
					let url = "/api/role/addRoleWithPerms";
					let saveData = {
						roleName : thiz.editFormData.roleName,
						discription : '',
						resources : resources
					}
					if(thiz.editFormData.isAdd && thiz.editFormData.isAdd == true){
						url = "/api/role/updateRole";
						saveData.roleId = thiz.editFormData.roleId;
					}
					$.ajax({
						type: 'POST',
						url: url,
						data: JSON.stringify(saveData),
						contentType: "application/json",
						headers: {'token': util.getToken()},
						dataType: 'json',
						success: function(data){
							util.checkLogin(data.code, thiz, function(){
								if(data && data.code === 0){
									thiz.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
									thiz.getRoleList();
									thiz.editFormData.formVisible = false;
								}else{
									thiz.$notify({title: '错误',message: data.msg, type: 'error',duration: 2000});
								}
							});
						},
						error: function(){
							thiz.$notify({title: '错误',message: "新增角色失败！", type: 'error',duration: 2000});
						}
					});
    			}
    		})
		},
		/*删除角色方法*/
		deleteRole: function(row){
			let thiz = this;
			thiz.$confirm('确认删除吗?', '提示', {
				type: 'warning'
			}).then(() => {
				let saveData = {
					roleId : row.id
				}
				$.ajax({
					type: 'POST',
					url: "/api/role/deleteRole",
					data: JSON.stringify(saveData),
					contentType: "application/json",
					dataType: 'json',
					headers: {'token': util.getToken()},
					success: function(data){
						util.checkLogin(data.code, thiz, function(){
							if(data && data.code == 0){
								thiz.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
								thiz.getRoleList();
							}
						});
					},
					error: function(){
						thiz.$notify({title: '错误',message: "新增角色失败！", type: 'error',duration: 2000});
					}
				});
			}).catch(() => {
				thiz.$notify({title: '错误',message: "删除角色失败！", type: 'error',duration: 2000});
			});
		}
	}
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	
	.toolbar {
		background: #fff;
	}
	.clearfix:after { content: "\0020"; display: block; height: 0; clear: both; visibility: hidden; }
	.clearfix { display: inline-block; }
	
</style>


