import babelpolyfill from 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import $ from 'jquery'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import VueRouter from 'vue-router'
import store from './vuex/store'
import Vuex from 'vuex'
import NProgress from 'nprogress'//页面顶部进度条
import 'nprogress/nprogress.css'

import Login from './components/Login.vue'
import Home from './components/Home.vue'
import Main from './components/Main.vue'

//import businessCount from './components/nav1/businessCount.vue'

//统计分析
import BusinessCount from './components/statistic-analysis/BusinessCount.vue' //客户业务量统计
import AutoValuationCount from './components/statistic-analysis/AutoValuationCount.vue' //自动估价服务统计
import OrgCostCount from './components/statistic-analysis/OrgCostCount.vue' //机构回价服务统计
import BuildingKeywordsCount from './components/statistic-analysis/BuildingKeywordsCount.vue' //关键词热度统计
//数据管理
import DataExport from './components/data-management/DataExport.vue' //数据导出
import DataImport from './components/data-management/DataImport.vue' //数据导出
import DataPremission from './components/data-management/DataPremission.vue' //数据导出
//系统管理
import RoleManagement from './components/sys-menagement/RoleManagement.vue' //角色管理
import UserManagement from './components/sys-menagement/UserManagement.vue' //用户管理



Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.use(Vuex)

const routes = [
  {
    path: '/login',
    component: Login,
    name: '登录',
    hidden: true//不显示在导航中
  },
  //{ path: '/main', component: Main },
  {
    path: '/',
    component: Home,
    redirect: '/login',
    name: '统计分析',
    iconCls: 'fa fa-bar-chart',//图标样式class
    children: [
      //{ path: '/main', component: Main },
      { path: '/BusinessCount', component: BusinessCount, name: '客户业务量统计' },
  	  { path: '/AutoValuationCount', component: AutoValuationCount, name: '自动估价服务统计' },
  	  { path: '/OrgCostCount', component: OrgCostCount, name: '机构回价服务统计' },
  	  { path: '/BuildingKeywordsCount', component: BuildingKeywordsCount, name: '关键词热度统计' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '数据管理',
    iconCls: 'fa fa-database',
    children: [
      { path: '/DataExport', component: DataExport, name: '数据导出' },
      { path: '/DataImport', component: DataImport, name: '数据导入' },
      { path: '/DataPremission', component: DataPremission, name: '数据权限' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '系统管理',
    iconCls: 'el-icon-setting',
    children: [
      { path: '/RoleManagement', component: RoleManagement, name: '角色管理' },
      { path: '/UserManagement', component: UserManagement, name: '用户管理' }
    ]
  }
]

let permissionData = $.parseJSON(localStorage.getItem("permission"));
if(permissionData != null && permissionData != undefined && permissionData.length > 0){
  $.each(routes, function(index, el) {
    if(permissionData.includes(el.name)){
      el.hidden = false;
    }else{
      el.hidden = true;
    }
    $.each(el.children,function(i, v) {
      if(permissionData.includes(v.name)){
        el.hidden = false;
        v.hidden = false;
      }else{
        v.hidden = true;
      }
    });
  });
}

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  NProgress.start();
  next()
  //console.log(to);
})

router.afterEach(transition => {
  NProgress.done();
});

new Vue({
  el: '#app',
  template: '<App/>',
  router,
  store,
  components: { App }
  //render: h => h(Login)
}).$mount('#app')

//router.replace('/login')




