<template>
	<el-row class="panel">
		<el-col :span="24" class="panel-top">
			<el-col :span="18" style="font-size:26px; line-height:60px;">
				<img src="../assets/logo4.png" class="logo"> <span>VQ<i style="color:#20a0ff">运营中心
				</i></span>
			</el-col>
			<el-col :span="6">
				<span style="color:#FFF; font-size:16px; margin:18px 10px 0 30px; float:right;cursor:pointer" @click="logout">退出</span>
				<el-dropdown @command="changeLoginStatus" style="color:#FFF;margin-top:20px; float:right; margin-left:20px;">
				  <span class="el-dropdown-link">
				    {{administrator}}<i :class="userIcon" style="color:#FFF; margin-left:5px; font-size:16px;cursor:pointer"></i>
				  </span>
				  <el-dropdown-menu slot="dropdown">
				    <el-dropdown-item command="1">我在线上<i class="el-icon-circle-check" style="color:green; margin-left:5px;"></i></el-dropdown-item>
				    <el-dropdown-item command="0">离线<i class="el-icon-circle-close" style="color:gray;margin-left:5px;"></i></el-dropdown-item>
				  </el-dropdown-menu>
				</el-dropdown>
				<el-badge is-dot class="item" style="margin-top:17px; float:right">
					<i class="fa fa-bell-o" style="font-size:26px; line-height:26px;"></i>
				</el-badge>
			</el-col>
		</el-col>
		<el-col :span="24" class="panel-center">
			<!--<el-col :span="4">-->
			<aside style="width:230px;">
				<el-menu style="border-top: 1px solid #475669;" :default-active="defaultActive" class="el-menu-vertical-demo" theme="dark" unique-opened router>
					<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
						<el-submenu :index="index+''">
							<template slot="title">
								<i :class="item.iconCls"></i>{{item.name}}
							</template>
							<el-menu-item v-for="child in item.children" :index="child.path" v-if="!child.hidden">{{child.name}}
							</el-menu-item>
						</el-submenu>
						<!-- <el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item> -->
					</template>
				</el-menu>
			</aside>
			<!--</el-col>-->
			<!--<el-col :span="20" class="panel-c-c">-->
			<section class="panel-c-c">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" style="margin-bottom:15px;">
						<el-breadcrumb separator="/" style="float:left;">
							<el-breadcrumb-item :to="{ path: '/table' }">首页</el-breadcrumb-item>
							<el-breadcrumb-item v-if="currentPathNameParent!=''">{{currentPathNameParent}}</el-breadcrumb-item>
							<el-breadcrumb-item v-if="currentPathName!=''">{{currentPathName}}</el-breadcrumb-item>
						</el-breadcrumb>
					</el-col>
					<el-col :span="24" style="background-color:#fff;box-sizing: border-box; padding: 15px">
						<!--<transition name="fade">-->
							<router-view></router-view>
						<!--</transition>-->
					</el-col>
				</div>
			</section>
			<!--</el-col>-->
		</el-col>
	</el-row>
</template>

<script>
	import util from '../common/util'
  export default {
  	created : function(){
  		let realName = localStorage.getItem("realName");
  		this.administrator = realName;
  		this.currentPathName = this.$router.history.current.name;
  		this.currentPathNameParent = this.$router.history.current.matched[0].name;
  		this.defaultActive = this.$router.history.current.fullPath;
  		this.loginName = localStorage.getItem("loginName");
  		let isOnline = localStorage.getItem("isOnline");
  		if(isOnline && isOnline == 1){
  			this.userIcon = 'el-icon-circle-check'
  		}else{
  			this.userIcon = 'el-icon-circle-close'
  		}
  	},
    data() {
      	return {
		  	currentPathName:'',
		  	currentPathNameParent:' ',
		  	administrator: '',
		  	defaultActive : '',
		  	loginName : '',
		  	userIcon : 'el-icon-circle-check'
      	}
    },
	watch: {
		'$route' (to, from) {//监听路由改变
			this.currentPathName=to.name;
			this.currentPathNameParent=to.matched[0].name;
		}
	},
    methods: {
		//退出登录
		logout:function(){
			var _this=this;
			this.$confirm('确认退出吗?', '提示', {
			}).then(() => {
				let loginName = _this.loginName;
				$.ajax({
					type: 'POST',
					data:{loginName : loginName},
					url: "/api/user/login_out",
					headers: {'token': util.getToken()},
					dataType: 'json',
					success: function(data){
						if(data && data != null){
							localStorage.setItem("isOnline", '1');
							util.checkLogin(data.code, _this, function(){
								if(data.code == 0){
									_this.$router.replace('/login');
								}
							});
						}
					}
				});
			}).catch(() => {
						
			});
		},
		changeLoginStatus: function(command){
			let _this = this;
			$.ajax({
				type: 'POST',
				data:{status : command},
				url: "/api/user/modify_login_status",
				headers: {'token': util.getToken()},
				dataType: 'json',
				success: function(data){
					if(data && data != null){
						localStorage.setItem("isOnline", '1');
						util.checkLogin(data.code, _this, function(){
							if(data.code == 0){
								_this.$notify({title: '成功',message: data.msg, type: 'success',duration: 2000});
								if(command == 1){
									localStorage.setItem("isOnline", '1');
									_this.userIcon = "el-icon-circle-check";
								}else{
									localStorage.setItem("isOnline", '0');
									_this.userIcon = "el-icon-circle-close";
								}
							}
						});
					}
				}
			});
		}
    }
  }
</script>

<style scoped>
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity .5s
	}
	
	.fade-enter,
	.fade-leave-active {
		opacity: 0
	}
	
	.panel {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
	}
	
	.panel-top {
		height: 60px;
		/*line-height: 60px;*/
		background: #1F2D3D;
		color: #c0ccda;
	}
	
	.panel-center {
		background: #324057;
		position: absolute;
		top: 60px;
		bottom: 0px;
		overflow: hidden;
	}
	
	.panel-c-c {
		background: #f1f2f7;
		position: absolute;
		right: 0px;
		top: 0px;
		bottom: 0px;
		left: 230px;
		overflow-y: scroll;
		padding: 20px;
	}
	
	.logout {
		background: url(../assets/logout_36.png);
		background-size: contain;
		width: 20px;
		height: 20px;
		float: left;
	}
	
	.logo {
		width: 40px;
		float: left;
		margin: 10px 10px 10px 18px;
	}
	
	.tip-logout {
		float: right;
		margin-right: 20px;
		padding-top: 5px;
	}
	
	.tip-logout i {
		cursor: pointer;
	}
	
	.admin {
		color: #c0ccda;
		text-align: center;
		float: left;
		line-height: 18px;
	}
</style>


